import { LISTA_LOCALIDADES } from './../../datos/codigos';
import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Platform } from 'ionic-angular';
import 'rxjs/add/operator/map';
import { Geolocation, GeolocationOptions, Geoposition } from '@ionic-native/geolocation';
import { NativeGeocoder } from '@ionic-native/native-geocoder';
import { ToastController } from 'ionic-angular';
/*
  Generated class for the PosicionProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PosicionProvider {

  geoOptions: GeolocationOptions;
  key: string = 'AIzaSyDLzt5gIIId8ceaCwlMXguzBWfo1u6cVIg';
  url: string = 'https://maps.googleapis.com/maps/api/geocode/json';
  url_geocode: string = 'https://maps.googleapis.com/maps/api/geocode/json';

  constructor(public http: Http, 
              private geolocation: Geolocation, 
              private platform: Platform, 
              private natGeo: NativeGeocoder,
              public toastCtrl: ToastController) {        
  }

  async getPosicion () {
    await this.platform.ready ();
    this.geoOptions = {
      maximumAge: 0,
      timeout: 5000,
      enableHighAccuracy: false
    };    
    return await this.geolocation.getCurrentPosition (this.geoOptions).then (data => {      
       return data;
    })
  };

  getLocalidad() {
    return this.getPosicion ().then (data => {      
      console.log (data);
      /*return this.natGeo.reverseGeocode (data.coords.latitude, data.coords.longitude).then (datos => {
        console.log (datos);
        this.muestraToast (datos.countryName, 'botton');
        return {codigo: LISTA_LOCALIDADES.indexOf(datos.locality.toLowerCase ()), localidad: datos.locality};
      })
      .catch (error => {
        console.log (error);
        //this.muestraToast ('ERROR:' + error.toString());
        return {codigo: 80, localidad: 'No posicionado'};
      })*/
      //return this.http.get (`${this.url}?latlng=${data.coords.latitude},${data.coords.longitude}&key=${this.key}`)       
      return this.http.get (`${this.url}?latlng=43.1187291,-6.2822751&key=${this.key}`)       
        .map (data => data.json())                  
    })
  }

  getCoordenadas (direccion: string) {
    return this.http.get (`${this.url_geocode}?address=${direccion}`)
      .map (data => data.json ())
  }

  muestraToast (texto: string, pos: string) {
    let toast = this.toastCtrl.create({
          message: texto,
          duration: 2000,
          position: pos
        });
        toast.present();
  }
}



